.SectionFirst {
  position: relative;
  top: 42px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url("../../../resources/Aguada.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 700px;
  width: 100%;
}

.SectionFirst .mainQuote {
  font-size: 2.5rem;
  color: white;
  display: block;
}

.SectionFirst .container-quote {
  display: inline-block;
  margin-left: 2rem;
}

.SectionFirst .container-quote .buttom {
  display: inline-block;
  margin-left: 4rem;
}

.buttom {
  font-size: 1rem;
  color: white;
  padding: 0.3rem;
  text-decoration: none;
  border-radius: 0.4rem;
  text-align: center;
  margin-top: 0.5rem;
  transition: 0.8s;
}

.buttom:hover,
.buttom:visited {
  background-color: rgba(230, 230, 250, 0.349);
}

.cardsContainer {
  height: 690px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}

.cardsContainer .containerHeaderCard {
  text-align: center;
  font-size: 1.5rem;
  color: rgba(12, 11, 11, 0.685);
}

.cardsContainer .cards {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.cardsContainer .cards .card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  gap: 0.5rem;
  height: 380px;
  flex-basis: 20%;
  padding: 1rem;
  border: 1px solid rgba(12, 11, 11, 0.685);
  border-radius: 0.5rem;
  transition: 1s;
  flex-wrap: wrap;
}

.cardsContainer .cards .card:hover {
  background-color: rgba(92, 166, 236, 0.774);
  color: white;
  transform: translateY(-15px);
}

.cardsContainer .cards .card:hover .iconCard {
  color: white;
}

.cardsContainer .cards .card .iconCard {
  font-size: 2rem;
  color: #1282eb;
  transition: 1s;
}

@media only screen and (max-width: 768px) {
  .cardsContainer {
    height: 2000px;
  }
  .container-quote .mainQuote,
  .containerHeaderCard h1 {
    font-size: 1.5rem;
  }

  .cardsContainer .cards {
    flex-wrap: wrap;
    gap: 4rem;
  }
  .cardsContainer .cards .card {
    height: 340px;
    flex-basis: 70%;
  }
}

@media only screen and (max-width: 480px) {
  .SectionFirst {
    height: 600px;
  }

  .SectionFirst .container-quote {
    margin-left: 0;
    text-align: center;
    width: 100%;
  }

  .SectionFirst .container-quote .buttom {
    display: inline-block;
    margin-left: 0;
    margin: 0.5rem;
  }
}
