.staff {
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 9rem 0 0 0;
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url("../../../resources/Aguada.jpg");
  height: 100rem;
}

.containerFlexManager {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.img {
  height: 150px;
  height: 150px;
  border-radius: 20rem;
}

.text {
  font-size: 1rem;
  color: whitesmoke;
}

.containerFlexCord {
  height: auto;
  display: flex;
  justify-content: space-around;
  align-content: flex-end;
  flex-wrap: wrap;
  align-items: center;
  font-size: 2rem;
  width: 50%;
  margin: 2rem auto;
  gap: 1rem;
  color: whitesmoke;
}

.cord {
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

.containerFlexWorker {
  height: 500px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  font-size: 1.5rem;
  color: whitesmoke;
}

.workerStyle {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  flex: 1 1 250px;
}

@media only screen and (max-width: 550px) {
  .staff {
    height: 190rem;
  }
}
