footer {
  border-top: 0.5px solid rgba(12, 11, 11, 0.685);
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: rgba(12, 11, 11, 0.685);
  padding: 2rem;
}

footer p {
  font-size: 0.8rem;
}

.iconsss {
  width: 60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 1.5rem;
}

.iconsss h3 {
  font-size: 1.5rem;
}

.iconsss a {
  padding: 0.5rem;
  text-align: center;
}

.iconsss a:hover {
  background-color: rgba(92, 166, 236, 0.774);
  border-radius: 0.5rem;
  transition: 1s;
}

.fas,
.fab {
  color: rgba(12, 11, 11, 0.685);
}

@media only screen and (max-width: 728px) {
  .iconsss {
    width: 100%;
  }
  .iconsss h3 {
    display: none;
  }
  .copyRight {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .iconsss h3 {
    display: none;
  }
}
