.navBar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  height: 3rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: white;
}

.navBar .logoContainer h1 {
  font-size: 1rem;
  text-decoration: none;
  color: rgba(12, 11, 11, 0.685);
}

.navBar .navTextContainer {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 50%;
  text-align: center;
}

.navBar .navTextContainer li a:link {
  text-decoration: none;
  font-size: 0.8rem;
  transition: 0.8s;
  border-radius: 0.5rem;
  padding: 0.25em;
  color: black;
  position: relative;
}

.navBar .navTextContainer li a::after {
  position: absolute;
  content: "";
  border-bottom: 1px solid rgba(92, 166, 236, 0.774);
  bottom: 0;
  left: 0;
  width: 0;
  transition: 0.5s;
}

.navBar .navTextContainer li a:visited {
  color: black;
}

.navBar .navTextContainer li a:hover::after {
  width: 100%;
}

.navBarMobile {
  border: none;
  cursor: pointer;
  display: none;
}

.navBarMobile li {
  background-color: rgba(12, 11, 11, 0.685);
  margin: 0.2rem 0;
  height: 0.3rem;
  width: 1.7rem;
  list-style: none;
}

#line {
  transition: 1s;
}

#line-1 {
  transform: rotate(42deg) translate(0.4rem, 0.3rem);
  transition: 1s;
}

#line-2 {
  opacity: 0;
  transition: 1s;
}

#line-3 {
  transform: rotate(-42deg) translate(0.4rem, -0.3rem);
  transition: 1s;
}

.navTextContainerMobile {
  transition: 1s;
  display: none;
}

.navTextContainerMobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: fixed;
  top: 3rem;
  height: 100%;
  width: 100%;
  opacity: 98%;
  z-index: 10;
  background-color: rgba(209, 215, 221, 0.97);
}

.nonActiveMobile {
  transform: translateX(-100%);
}

.navTextContainerMobile button:hover {
  background-color: rgba(92, 166, 236, 0.774);
  color: white;
  transition: 0.8s;
}

.navTextContainerMobile li,
.navTextContainerMobile button {
  color: black;
  margin: 0 auto;
  list-style: none;
  font-size: 0.8rem;
}

.navTextContainerMobile li a {
  text-decoration: none;
  color: black;
  padding: 0.5rem;
}

.navTextContainerMobile li a:hover {
  background-color: rgba(92, 166, 236, 0.94);
  color: white;
  transition: 0.8s;
  border-radius: 0.5rem;
}

@media only screen and (max-width: 768px) {
  .navBar .navTextContainer {
    width: 60%;
  }
}

@media only screen and (max-width: 480px) {
  .navBarMobile {
    display: block;
    margin-right: 2rem;
  }
  .navBar {
    justify-content: space-between;
  }
  .navBar .logoContainer h1 {
    margin-left: 2rem;
  }
  .navBar .navTextContainer {
    display: none;
  }
}
