.contact {
  padding-top: 4rem;
  height: 1000px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url("../../../resources/Aguada.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  color: rgba(12, 11, 11, 0.685);
  flex-wrap: wrap;
}

.contact-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  border-radius: 1rem;
  font-size: 0.7rem;
  height: 22rem;
  background-color: whitesmoke;
  width: 240px;
  opacity: 0.92;
  transition: 1s;
}

.contact-card:hover {
  background-color: rgba(34, 137, 233, 0.8);
  transform: translateY(-10px);
  color: whitesmoke;
}

.contact-card:hover > a {
  color: whitesmoke;
}

.i {
  font-size: 2rem;
  color: royalblue;
}

@media only screen and (max-width: 550px) {
  .contact {
    height: 1300px;
  }
}
