.who {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url("../../../resources/Aguada.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  align-content: center;
  height: 800px;
  width: 100%;
  overflow: auto;
}

.who .contTitle {
  width: 40%;
  text-align: left;
}

.who .contTitle .title1 {
  color: whitesmoke;
  font-size: 2rem;
  margin-bottom: 0.9rem;
}

.who .contTitle .para1 {
  color: whitesmoke;
  font-size: 0.9rem;
  text-align: justify;
}

.who .contImg {
  display: flex;
  text-align: center;
  justify-content: center;
  width: 40%;
}

.who .contImg .office {
  flex-basis: 22px;
  height: 250px;
  max-width: 100%;
  border-radius: 20px;
}

@media only screen and (max-width: 768px) {
  .who {
    height: 900px;
    flex-wrap: wrap;
    align-content: center;
    gap: 2rem;
  }
  .who .contTitle {
    width: 90%;
  }

  .who .contImg {
    margin-top: 3%;
    width: 90%;
  }
}
